// reset
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
}

// postavljanje osnovnih stilova i fontova
body {
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  overflow-x: hidden;
  @include tablet {
    overflow-x: hidden;
    overflow-y: auto;
  }
}
h1,
h2,
h3,
h4,
h5 {
  font-family: "Oswald", sans-serif;
}
// navigacija za mobilne uredjaje
#wrapper {
  display: none;
  @include tabletL {
    overflow: hidden;
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    transition: width 0.5s ease;
    background: linear-gradient(to left bottom, #0f2027, #203a43, #2c5364);
    width: 0;
    z-index: 2;
    @include flex(column, center, center, false);
    .telefon {
      height: 100vh;
      @include flex(column, space-around, center, false);
      a {
        position: relative;
        color: #dadada;
        font-size: 2rem;
        opacity: 0.8;
        font-family: "Oswald", sans-serif;
      }
    }
  }
}

#button {
  visibility: hidden;
  @include tabletL {
    visibility: visible;
    z-index: 99;
    position: fixed;
    right: 30px;
    top: 20px;
    cursor: pointer;
    width: 50px;
    height: 38px;
    #prva,
    #druga,
    #treca {
      height: 4px;
      position: absolute;
      left: 5px;
      width: 40px;
      cursor: pointer;
      transition: all 0.5s ease;
      background: #dadada;
    }
    #prva {
      top: 5px;
    }
    #druga {
      top: 17px;
    }
    #treca {
      top: 29px;
    }
  }
}
//main

main {
  position: relative;
  width: 100%;
  overflow-x: hidden;
  nav {
    width: 100%;
    height: 80px;
    @include flex(row, space-between, center, false);
    background: transparent;
    position: fixed;
    top: 0;
    z-index: 1;
    transition: background-color 0.2s ease-in;
    .logo {
      position: relative;
      top: 30px;
      left: 20px;
      padding: 5px;
      background: #333;
      border: 2px solid #fff;
      box-shadow: 0 0 20px 5px #dadada;
      border-radius: 50%;
      @include flex(row, center, center, false);
      z-index: 99;
      img {
        width: 120px;
        @include tabletL {
          width: 80px;
        }
      }
      &:hover {
        animation: logo-glow 1s infinite;
      }
    }
    .meni {
      width: 100%;
      @include flex(row, flex-end, center, false);
      z-index: 9;
      @include tabletL {
        display: none;
      }
      a {
        line-height: 1em;
        margin: 0 30px;
        position: relative;
        color: #fff;
        font-size: 2rem;
        opacity: 0.8;
        font-family: "Oswald", sans-serif;
        transition: 0.2s ease-in;
        &:after {
          position: absolute;
          content: " ";
          width: 0;
          height: 3px;
          background: #dadada;
          bottom: -2px;
          left: 0;
          transition: 0.2s ease-in;
        }
        &:hover {
          &:after {
            width: 100%;
          }
        }
      }
    }
  }
  .sticky {
    background: linear-gradient(to left bottom, #0f2027, #203a43, #2c5364);
    -webkit-box-shadow: 0px 5px 19px 0px #000000;
    box-shadow: 0px 5px 19px 0px #000000;
  }
}
// ----- pocetna
#particles-js {
  @include flex(row, center, flex-start, false);
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.1)),
    url(../img/hero.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  box-shadow: 0 10px 16px -8px rgba(0, 0, 0, 0.75);
  .banner-under {
    position: absolute;
    background: linear-gradient(
      -15deg,
      rgb(48, 54, 50) 0%,
      rgb(81, 83, 81) 45%,
      rgb(138, 141, 139) 55%,
      rgb(81, 83, 81) 75%,
      rgb(48, 54, 50) 100%
    );
    opacity: 0.85;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 640px;
    height: 520px;
    clip-path: polygon(18% 10%, 85% 22%, 95% 70%, 52% 92%, 15% 72%, 0% 60%);
    @include tabletL {
      left: 50%;
      width: 600px;
    }
    @include tablet {
      display: none;
    }
    @include telefon {
      display: none;
    }
  }
  .banner {
    position: absolute;
    background: linear-gradient(
      120deg,
      rgba(255, 255, 255, 1) 0%,
      rgba(218, 218, 218, 1) 55%,
      rgba(255, 255, 255, 1) 65%,
      rgba(233, 233, 233, 1) 75%,
      rgba(190, 190, 190, 1) 100%
    );
    opacity: 0;
    padding: 80px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    clip-path: polygon(5% 5%, 81% 0, 100% 50%, 92% 97%, 17% 100%, 0% 50%);
    @include tabletL {
      // left: 80%;
      width: 600px;
    }
    @include tablet {
      clip-path: none;
      width: 70%;

      // transform: translate(25%, -50%);
    }
    @include telefon {
      clip-path: none;
      width: 100%;
      left: 0;
      transform: translate(0, -50%);
      padding: 40px 20px;
    }
    h1 {
      font-size: 3rem;
      color: #181815;
      @include tabletL {
        font-size: 2.5rem;
      }
      @include tablet {
        font-size: 2.2rem;
      }
      @include telefon {
        text-align: center;
      }
      span {
        color: #2c5364;
      }
    }
  }
  nav {
    width: 100%;
    height: 80px;
    @include flex(row, space-between, center, false);
    @include telefon {
      width: 100%;
    }
    .lang {
      @include flex(row, space-between, center, false);
    }
    .logo {
      top: 10px;
      left: 20px;
      background: #222;
      padding: 5px;
      border: 2px solid rgba(255, 214, 5, 0.4);
      box-shadow: 0 0 20px 5px rgba(255, 214, 5, 0.5);
      border-radius: 50%;
      @include flex(row, center, center, false);
      z-index: 99;
      img {
        width: 120px;
        @include tabletL {
          width: 80px;
        }
      }
      &:hover {
        animation: logo-glow 1s infinite;
      }
    }
  }
}
#particles-js.active {
  transition: left 0.5s ease-out;
  left: 0;
}
